var projects = [{ title: 'tmux_pm25', sub: '在Tmux状态栏中显示PM2.5指数', link: 'https://github.com/DingDean/tmux_pm25', content: '<p>我的开发环境是典型的Tmux + Vim。</p> <p>所以我基本上“生活”在Tmux里。</p> <p>空气好的时候，我喜欢在编程之余去户外跑步。</p> <p>之前我获取空气质量的方法开销太大：</p> <p>找到手机，打开空气应用，滑动找到空气质量。</p> <p>不仅步骤多，同时分散注意力的风险也大。</p> <p>现在这个小插件让我瞥一眼就能获取信息。</p> <p>如果空气质量不好，我可以马上回到编程的状态。</p> ' }, { title: '数据为镜', sub: '记录和分析编程足迹', link: 'https://github.com/DingDean/digitalme', content: '<p>在这个时代，每个人多少都可以被其数字足迹所描述。</p> <p>听了多少音乐，买了多少书籍，每份足迹都描述一个人的某个位面。</p> <p>这个项目受wakatime启发，试图记录自己的编程足迹，从而定量的回答一些问题：</p> <p>经常使用的编程语言有哪些？</p> <p>每天有多少时间用于编程？</p> <p>什么时间段编程最有效率？</p> <p>喜欢使用哪些技术？</p> <p>……</p> <p>希望数据能成为一面镜子，呈现一个真实的自我。</p>' }];

